import { Inline, Node } from '@ali/4ever-cangjie';
import { JsonML } from '@ali/4ever-utils';
import { omit } from 'lodash-es';
import Tag from '../models';

export default {
  match(node: Node): boolean {
    return Tag.isTag(node);
  },
  convert(node: Inline): JsonML {
    // @ts-ignore
    // 河图插件兼容排版引擎 1.0 加的 hack
    const data = omit(node.data, '__HACK')
    return ['tag', data];
  },
};
